import baseAdd from 'components/base-add.vue';
import {vacationInfo} from '@/api/vacation';
import {helpQuery} from '@/api/help';

export default {
  name: 'vacation',
  components: {
    baseAdd
  },
  data() {
    return {
      vacationRecord:[],
      isHelpOpen:false,
      message:'',
    }
  },
  methods: {
    addVacation(){
      this.$router.push('/vacationInfo');
    },
    vacationDetail(){
      this.$router.push('/vacationDetail');
    },
    openHelp(){
      this.isHelpOpen = true;
    },
    closeHelp(){
      this.isHelpOpen = false;
    },
  },
  mounted() {
    vacationInfo({
      "hrEmpId": "22",
      "hrApplyId": "123",
      "hrApplyStatus":"22"
    }).then(response => {
      if (response.data && response.data.rows) {
        this.vacationRecord = response.data.rows;
        this.isLoadingData = false;
      } else {
        this.isLoadingData = false;
      }
    }).catch(error => {
      this.isLoadingData = false;
    });
    helpQuery({
      "page":"1",
      "pagesize":"10",
      "hrPageconfCode":"B001"
    }).then(response => {
      if (response.data && response.data) {
        this.message = response.data.rows[0].hrPageconfDocs;
        /* console.log("这里是helpList"+ JSON.stringify(this.message));*/
      } else {
      }
    }).catch(error => {
    });
  }
}



